.todo-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  width: 90%;
  min-height: 500px;
  padding: 50px 20px;
  background-color: #dcd7cbd4;
  border-radius: 8px;
}
.todo-list__wrapper {
  display: flex;
  gap: 20px;
  width: 80%;
  justify-content: space-between;
}
.list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 80%;
}
.record {
  background-color: #f5f5f5;
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  padding: 10px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  transition: all 0.3s ease;
}
.record:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.record-input {
  height: 35px;
  flex: 1;
  border: 1px solid #000;
  padding: 0 10px;
  border-radius: 4px;
}
.select {
  width: 80px;
  height: 35px;
  border: 1px solid #000;
  border-radius: 4px;
}
.select:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.record-btn-add {
  height: 35px;
  border: 1px solid #000;
  border-radius: 4px;
}
.record-btn-add:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
